import React from "react";
import { Container, Row, Col, Form, Button } from "react-bootstrap";

class FormUser extends React.Component {
  constructor(props) {
    super(props);
    this.handleSubmit = this.handleSubmit.bind(this);
    console.log({ props });
  }

  handleSubmit(e) {
    e.preventDefault();
    const form = e.target;

    const obj = {
      name: form.name.value,
      birthday: form.birthday.value,
      address: form.address.value,
      city: form.city.value
    };

    localStorage.setItem("user", JSON.stringify(obj));

    this.props.handleform(obj);
  }

  render() {
    return (
      <Container className="paper">
        <Row>
          <Col xs={12} className="section">
            <h1>Formulaire</h1>
            <h2>Attestation de déplacement dérogatoire</h2>
          </Col>
          <Col xs={12} className="section">
            <Form className="text-right" onSubmit={this.handleSubmit}>
              <Form.Group as={Row}>
                <Form.Label column sm="2">
                  Mme / M.
                </Form.Label>
                <Col sm="10">
                  <Form.Control
                    type="text"
                    defaultValue={this.props.form.name}
                    placeholder="Nom, prénom"
                    name="name"
                  />
                </Col>
              </Form.Group>
              <Form.Group as={Row}>
                <Form.Label column sm="2">
                  Né(e) le :
                </Form.Label>
                <Col sm="10">
                  <Form.Control
                    type="date"
                    name="birthday"
                    defaultValue={this.props.form.birthday}
                  />
                </Col>
              </Form.Group>
              <Form.Group as={Row}>
                <Form.Label column sm="2">
                  Demeurant :
                </Form.Label>
                <Col sm="10">
                  <Form.Control
                    type="text"
                    placeholder="Adresse complète"
                    name="address"
                    defaultValue={this.props.form.address}
                  />
                </Col>
              </Form.Group>
              <Form.Group as={Row}>
                <Form.Label column sm="2">
                  Fait à :
                </Form.Label>
                <Col sm="10">
                  <Form.Control
                    type="text"
                    placeholder="Ville"
                    name="city"
                    defaultValue={this.props.form.city}
                  />
                </Col>
              </Form.Group>
              <Col sm={{ span: 10, offset: 2 }} className="text-left p0">
                <Button variant="primary" type="submit">
                  Aperçu
                </Button>
              </Col>
            </Form>
          </Col>
        </Row>
      </Container>
    );
  }
}

export default FormUser;
